.index-product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
}

.index-product-card {
  border: 1px solid #eaeaea;
  border-radius: 12px;
  overflow: hidden;
  text-align: center;
  background-color: #fff;
  transition: box-shadow 0.2s ease;
}

.index-product-card:hover {
  box-shadow: 0 0 10px rgba(0,0,0,0.08);
}

.index-product-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.index-product-info {
  padding: 10px 15px;
}

.index-product-name {
  font-size: 15px;
  margin: 10px 0 5px;
  font-weight: 500;
}

.index-product-price {
  color: #e74c3c;
  font-weight: bold;
  margin-bottom: 10px;
}

.index-btn-detail {
  display: inline-block;
  font-size: 13px;
  padding: 6px 12px;
  background-color: #f8f9fa;
  color: #333;
  border-radius: 20px;
  border: 1px solid #ccc;
  text-decoration: none;
}

.index-btn-detail:hover {
  background-color: #e9ecef;
}
